import { NextResponse } from "next/server";
import mongoose from "mongoose";
import dbConnect from "@/lib/mongodb";
import Property from "@/models/property";

export async function GET() {
  try {
    await dbConnect();
    const properties = await Property.find().populate("owner"); // Populate the owner field
    return NextResponse.json(properties);
  } catch (error) {
    console.error("Error fetching properties:", error);
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}

export async function POST(request) {
  try {
    await dbConnect();
    const data = await request.json();

    // Convert owner to ObjectId
    if (!mongoose.Types.ObjectId.isValid(data.owner)) {
      return NextResponse.json({ error: "Invalid owner ID" }, { status: 400 });
    }

    data.owner = new mongoose.Types.ObjectId(data.owner); // Convert owner to ObjectId
    const property = await Property.create(data);

    return NextResponse.json(property, { status: 201 });
  } catch (error) {
    console.error("Error creating property:", error);
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}
